import React from "react"
import { Link, useStaticQuery, graphql } from "gatsby"

import Layout from "../components/layout"
import Section from "../components/section"
import SEO from "../components/seo"
import { BrowserView, MobileView } from "react-device-detect"
import { Card, Row, Col, Button } from "react-materialize"
import Img from "gatsby-image"

const IndexPage = () => {
  const images = useStaticQuery(graphql`
    query {
      front_menu_image: file(relativePath: { eq: "main_page.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      front_map: file(relativePath: { eq: "ranken_map.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      under_construction: file(relativePath: { eq: "under_construction.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  const mobileView = (
    <MobileView>
      <Layout>
        <Section
          title="Dine In"
          subtitle="Authentic ramen. Simmered to perfection."
          body=""
          to="menu"
          color="#0F0F0F"
          textColor="white"
          label="Menu"
        />
        <Section
          title="Carry Out"
          subtitle="Zero fees. Skips the line. Saves time."
          body=""
          color="white"
          textColor="#050505"
          label="Order Now"
          external="http://www.mealage.com/m.jsp?id=8155"
        />
        <Section
          title="Locate"
          subtitle="Find some Ramen Near You!"
          body=""
          to="locate"
          color="#404040"
          textColor="white"
          label="Find some Ramen Near You!"
        />
      </Layout>
    </MobileView>
  )

  return (
    <>
      <SEO title="Home" />
      <BrowserView>
        <Layout>
          <Row style={{ width: "80%", marginTop: "5%" }}>
            <Col s={2}></Col>
            <Col s={8}>
              <Row>
                <Col s={6}>
                  <Card
                    actions={[]}
                    header={
                      <Img
                        fluid={images.front_menu_image.childImageSharp.fluid}
                      />
                    }
                  ></Card>
                </Col>
                <Col s={1}></Col>
                <Col s={5}>
                  <div
                    style={{
                      height: "50%",
                      marginTop: "20%",
                      paddingTop: 150,
                      paddingBottom: 200,
                      width: "100%",
                    }}
                  >
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "space-evenly",
                        paddingTop: "3%",
                        paddingBottom: "8%",
                        fontFamily: "calibri",
                        fontSize: "2em",
                      }}
                    >
                      Authentic Japenese Cuisine to make you fall in love.
                    </div>

                    <div
                      style={{
                        display: "flex",
                        paddingTop: 10,
                        paddingBottom: 10,
                        flexDirection: "column",
                      }}
                    >
                      <div style={{ paddingTop: 10, paddingBottom: 10 }}>
                        <a href="http://www.mealage.com/m.jsp?id=8155">
                          <Button
                            style={{
                              shadow: 0,
                              backgroundColor: "#f3a140",
                              paddingLeft: 50,
                              paddingRight: 50,
                            }}
                          >
                            Carry Out
                          </Button>
                        </a>
                      </div>
                      <div style={{ paddingTop: 10, paddingBottom: 10 }}>
                        <a href="http://doordash.com">
                          <Button
                            style={{
                              backgroundColor: "#f3a140",
                              paddingLeft: 50,
                              paddingRight: 50,
                            }}
                          >
                            Delivery
                          </Button>
                        </a>
                      </div>
                    </div>
                  </div>
                </Col>
              </Row>
            </Col>
          </Row>
        </Layout>
      </BrowserView>
      {mobileView}
    </>
  )
}

export default IndexPage
